body {
  font-family: Arial, sans-serif;
}

#pagination {
  text-align: center;
  margin-top: 20px;
}

#pagination button {
  cursor: pointer;
  background-color: red;
  border: 1px solid #ccc;
  margin: 0 4px;
  padding: 8px 12px;
}

#pagination button:hover {
  background-color: #e0e0e0;
}

#pagination button.active {
  color: green;
  background-color: #007bff;
}

@media (max-width: 767px) {
  #pagination button {
    margin: 0 2px;
    padding: 6px 8px;
  }
}

